.tabs {
    display: flex;
    grid-gap: 1rem;
    width: 25%;
    padding: .5rem;
    .tab{
      padding: 10px;
      margin-right: 10px;
      cursor: pointer;
    }
  }
  .active{
    background-color: #e7e7e7;
    border-radius: 7px;
  }
  .tab-content {
    margin-top: 10px;
  }
  